.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #6988524a;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

div.card {
  background: rgb(138, 255, 165);
  border: 1px solid #999;
  float: left;
  font-size: 26px;
  font-weight: bold;
  line-height: 34px;
  height: 134px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  color: #000000;
  width: 110px;
  -ms-writing-mode: tb-rl;
  writing-mode: vertical-rl !important;
  text-orientation: upright;
  word-wrap: break-word;
  text-align: start;

  &:nth-child(3n+1) {
    clear: both;
  }
  &:nth-child(n+2):not(:nth-child(3n+1)) {
    margin-left: 10px;
  }
  &:nth-child(n+4) {
    margin-top: 10px;
  }
  
}


.card:focus {
  outline: none;
}

.kbd-navigation .card:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}
